.stat_list {
  display: block;

  max-width: 300px;
  height: auto;

  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  padding: 5px;

  background-color: #fff;
  box-shadow: 0 0 5px rgb(0 0 0 / 10%);
}

.item {
  display: flex;
  position: relative;

  margin-bottom: 15px;

  background-color: #fff;
  box-shadow: 0 0 5px rgb(0 0 0 / 10%);
}

.status {
  position: absolute;
  top: 33%;
  left: 5px;

  width: 18px;
  height: 18px;

  margin-right: 15px;

  border: 1px solid white;
  border-radius: 50px;

  vertical-align: middle;

  background-color: red;
}

.onLine {
  position: absolute;
  top: 33%;
  left: 5px;

  width: 18px;
  height: 18px;

  margin-right: 15px;

  border: 1px solid white;
  border-radius: 50px;

  vertical-align: middle;

  background-color: green;
}

.offLine {
  position: absolute;
  top: 33%;
  left: 5px;

  width: 18px;
  height: 18px;

  margin-right: 15px;

  border: 1px solid white;
  border-radius: 50px;

  vertical-align: middle;

  background-color: red;
}

.avatar {
  left: 150px;
  border-radius: 5px;
  border: 1px solid green;

  margin-right: 10px;
  margin-left: 30px;
}

.name {
  font-weight: 400;
  font-size: 18px;
  line-height: 1.21;
}
