.profile {
  display: block;

  max-width: 300px;
  height: auto;

  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  padding-top: 25px;

  background-color: #fff;
  box-shadow: 0 0 5px rgb(0 0 0 / 10%);
}

.avatar {
  display: block;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
}

.name {
  text-align: center;

  font-weight: 700;
  font-size: 24px;
  line-height: 1.21;
}

.tag {
  text-align: center;

  font-weight: 700;
  font-size: 12px;
  line-height: 1.21;

  color: darkgrey;
}

.location {
  text-align: center;

  font-weight: 700;
  font-size: 18px;
  line-height: 1.21;

  color: darkgrey;
}

.stats {
  display: flex;

  background-color: #f5f5f5;
}

.stats > li {
  display: inline-block;

  width: calc(100% / 3);

  padding: 5px;
  padding-top: 25px;
  padding-bottom: 25px;

  box-shadow: 0 0 5px rgb(0 0 0 / 10%);
}

.label {
  display: block;

  margin-bottom: 10px;

  text-align: center;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.21;

  color: darkgrey;
}

.quantity {
  display: block;

  text-align: center;
  font-weight: 900;
  font-size: 18px;
  line-height: 1.21;
}
